.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: var(--padding-horizontal);
  overflow-x: hidden;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  background: 'linear-gradient(180deg, #ADCE69 0%, #56B892 100%)';
}

.mission {
  position: relative;
}

.header {
  text-align: center;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.settings_btn {
  width: calc(var(--game-width) * 0.08);
}

.btn {
  width: calc(var(--game-width) * 0.3);
}

.btn_title {
  z-index: 1;
  margin: auto;
  text-shadow: -1px 2px 0px #000072;
  line-height: calc(var(--game-width) * 0.3 * 0.4);
  font-size: var(--fs-20);
}

.btn_bg {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.bottom {
  margin-top: auto;
}

.username {
  font-size: var(--fs-16);
}

.title {
  font-size: var(--fs-24);
  text-transform: uppercase;
  margin-top: calc(var(--game-width) * 0.1);
  line-height: calc(var(--game-width) * 0.02);
  z-index: 2;
}

.username {
  font-size: var(--fs-16);
}

.content {
  /* position: absolute; */
  margin: calc(18.8vh - var(--game-width) * 0.12) 0 calc(var(--padding-horizontal) * 2) 0;
  display: flex;
  flex: 1;
  width: 100%;
  overflow: auto;
  padding: 0 calc(var(--padding-horizontal) * 1);
  font-family: SVNGilroyRegular;
  display: flex;
  flex-wrap: wrap;
}

.tabs {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  position: relative;
}

.tab_btn {
  margin-bottom: calc(var(--game-width) * 0.02);
  width: calc(var(--game-width) * 0.43);
  position: relative;
  z-index: 3;
}

.tab_btn_title {
  z-index: 1;
  text-shadow: -1px 2px 0px #000072;
  font-size: var(--fs-18);
  padding: calc(var(--padding-horizontal) * 0.8) calc(var(--padding-horizontal) * 0.5);
  line-height: calc(var(--game-width) * 0.43 * 0.2);

  width: 100%;
  margin: auto;
}

.btn_bg {
  width: 100%;
  position: absolute;
  height: calc(var(--game-width) * 0.43 * 0.3);
  top: 0;
  left: 0;
}

.moc1 {
  position: absolute;
  width: calc(var(--game-width) * 0.038);
  top: calc(var(--game-width) * 0.295 * 0.32);
  left: calc(var(--game-width) * 0.03);
}

.moc2 {
  position: absolute;
  width: calc(var(--game-width) * 0.038);
  top: calc(var(--game-width) * 0.295 * 0.32);
  right: calc(var(--game-width) * 0.03);
}
