.itemContainer {
  width: 48%;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(173.16deg, #ffffff 5.35%, #dbe1e8 101.95%, #e7f0fb 101.95%);
  border: 1px solid #158a5a;
  border-radius: 10px;
  margin-bottom: calc(var(--padding-horizontal) * 1.5);
  max-height: fit-content;
  display: flex;
}

.giftImg {
  height: calc(var(--game-width) * 0.3);
  margin-bottom: calc(var(--padding-horizontal) * 1.5);
  margin-top: calc(var(--padding-horizontal) * 1.5);
}

.bigGiftImg {
  height: calc(var(--game-width) * 0.3);
  margin-bottom: calc(var(--padding-horizontal) * 1.5);
  margin-top: calc(var(--padding-horizontal) * 1.5);
}

.bottom {
  /* background: linear-gradient(180deg, #d81c23 0%, #9b030a 100%); */
  border-radius: 0px 0px 9px 9px;

  width: 100%;
  padding-top: calc(var(--padding-horizontal) * 1.5);
  padding-bottom: var(--padding-horizontal);
  text-align: center;
  margin-top: calc(var(--padding-horizontal) * 1);
  height: fit-content;
  margin-top: auto;
}

.content {
  padding-top: calc(var(--padding-horizontal * 1.5));
  padding-bottom: calc(var(--padding-horizontal * 1.5));
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  flex: 1;
}

.value {
  color: #000072;
  font-size: var(--fs-12);
}

.count {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: var(--fs-16);
  justify-content: center;
}

.name {
  font-size: var(--fs-16);
}

.text {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: flex-start;
  margin-bottom: auto;
}
