.container {
  position: relative;
  width: calc(var(--game-width) * 0.84);
  height: calc(var(--game-width) * 0.9 * 473 / 394);
  background-size: 100% 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* padding: 0px calc(var(--game-width) * 0.07); */
  padding-top: calc(var(--game-width) * 0.18);
  margin-top: -15%;
}

.closeButton {
  width: calc(var(--game-width) * 0.085);
  height: calc(var(--game-width) * 0.085);
  background-size: 100% 100%;
}

.header {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 5%;
  justify-content: center;
}

.toggleOn img {
  filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(7488%) hue-rotate(130deg)
    brightness(102%) contrast(104%);
}

.ruleLink {
  font-size: var(--fs-18);
  color: #000072;
  text-decoration: underline;
  text-underline-offset: calc(var(--game-width) * 0.015);
  margin-top: 17%;
  cursor: pointer;
}

.custom {
  font-size: var(--fs-24);
}

.gift {
  margin-top: calc(var(--padding-horizontal) * 3);
  height: calc(var(--game-width) * 0.4);
}

.bottom {
  width: 99%;
  margin-left: 1%;
  margin-top: calc(var(--padding-horizontal) * 1);
  display: flex;
  align-items: center;
  flex-direction: column;
}

.date {
  margin-bottom: calc(var(--padding-horizontal) * 1);
  color: #000072;
  font-size: var(--fs-16);
}

.congrat {
  color: #000;
  font-size: var(--fs-14);
}

.btn {
  width: calc(var(--game-width) * 0.26);
  z-index: 4;
  margin-right: calc(var(--padding-horizontal) * 0.5);
}

.btn_title {
  z-index: 4;
  margin: auto;
  text-shadow: -1px 2px 0px #000072;
  line-height: calc(var(--game-width) * 0.26 * 0.4);
  font-size: var(--fs-13);
}

.done_btn_title {
  z-index: 4;
  margin: auto;
  color: #000072;
  line-height: calc(var(--game-width) * 0.26 * 0.26);
  font-size: var(--fs-13);
}

.btn_bg {
  z-index: 4;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.btns {
  display: flex;
  width: 100%;
  padding: 0 calc(var(--padding-horizontal) * 4);
  justify-content: space-between;
}

.done {
  margin-top: calc(var(--padding-horizontal) * 1.5);
  color: #000072;
  font-size: var(--fs-20);
}

.tvp {
  margin-bottom: calc(var(--padding-horizontal) * 1);
  color: #000072;
  font-size: var(--fs-20);
}
