.container {
  position: relative;
  width: calc(var(--game-width) * 0.94);
  height: calc(var(--game-width) * 0.94 * 502 / 397);
  background-size: 100% 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info {
  width: 100%;
  height: 78%;

  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 13%;

  overflow-x: hidden;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.info::-webkit-scrollbar {
  display: none;
}

.title {
  color: #d81c23;
  font-size: var(--fs-24);
  text-align: center;
}

.subTitle {
  color: #000072;
  font-size: var(--fs-20);
  text-align: center;
  margin-top: 2%;
}

.banner {
  width: 83%;
  margin-top: 8%;
}

.actionRow {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 2%;
  padding: 0 8%;
}

.actionButton {
  width: calc(var(--game-width) * 0.38);
  height: calc(var(--game-width) * 0.38 * 65 / 161);
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: var(--fs-20);
  text-shadow: calc(var(--game-width) * -0.003) calc(var(--game-width) * 0.003 * 2) 0px
    rgba(155, 3, 10, 0.6);
}

.exitButton {
  width: calc(var(--game-width) * 0.08);
  height: calc(var(--game-width) * 0.08);
  background-size: 100% 100%;
}
