.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: var(--padding-horizontal);

  overflow-x: hidden;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  overflow-y: hidden;
  position: relative;
}

.header {
  text-align: center;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.settings_btn {
  width: calc(var(--game-width) * 0.08);
}

.btn {
  width: calc(var(--game-width) * 0.35);
}

.btn_title {
  z-index: 1;
  margin: auto;
  text-shadow: -1px 2px 0px #000072;
  line-height: calc(var(--game-width) * 0.35 * 0.4);
  font-size: var(--fs-20);
}

.btn_bg {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.content {
  /* position: absolute; */
  margin: calc(20vh - var(--game-width) * 0.12) var(--padding-horizontal)
    calc(var(--padding-horizontal) * 2) var(--padding-horizontal);
  display: flex;
  flex: 1;
  overflow: auto;
  background: linear-gradient(173.16deg, #ffffff 5.35%, #dbe1e8 101.95%, #e7f0fb 101.95%);
  border: 1px solid #024436;
  border-radius: 10px;
  padding: calc(var(--padding-horizontal) * 1.5);
  font-family: SVNGilroyRegular;
  z-index: 2;
}

.bottom {
  margin-top: auto;
  z-index: 2;
}

.ruleContent {
  font-size: var(--fs-14);
  color: #272727;
}

.username {
  font-size: var(--fs-16);
}

.title {
  font-size: var(--fs-24);
  text-transform: uppercase;
  margin-top: calc(var(--game-width) * 0.1);
  line-height: calc(var(--game-width) * 0.02);
  z-index: 2;
}

.username {
  font-size: var(--fs-16);
}
