.container {
  position: absolute;
  z-index: 3;
  top: 0;
  background-color: rgba(1, 1, 1, 0.8);
  overflow: hidden;
}

.hide {
  visibility: hidden;
  background-size: 200% 200%;
  opacity: 0;
}

.show {
  visibility: visible;
  width: var(--game-width);
  height: var(--app-height);
  background-size: 100% 100%;
  opacity: 1;
}

.content {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.quote1 {
  width: calc(var(--ingame-width) * 0.8);
  height: calc(var(--ingame-width) * 0.8 * 133 / 328);
  background: linear-gradient(173.16deg, #ffffff 5.35%, #dbe1e8 101.95%, #e7f0fb 101.95%);
  border-radius: calc(var(--ingame-width) * 0.05);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 88%;
  text-align: center;
  cursor: pointer;
  text-align: center;
}

.quote1 span:nth-child(1) {
  font-size: var(--fs-14-2);
  color: #000072;
  margin-top: 1%;
}

.quote1 span:nth-child(2) {
  font-size: var(--fs-16-2);
  color: #158a5a;
  margin-top: 7%;
}

.demoCat {
  height: calc(var(--ingame-width) * 0.6);
  margin-top: -4%;
}

.demoTopBar {
  width: 100%;
  margin-top: 6%;
}

.quote2 {
  width: calc(var(--ingame-width) * 0.8);
  height: calc(var(--ingame-width) * 0.8 * 164 / 328);
  background: linear-gradient(173.16deg, #ffffff 5.35%, #dbe1e8 101.95%, #e7f0fb 101.95%);
  border-radius: calc(var(--ingame-width) * 0.05);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 1.5%;
  text-align: center;
  cursor: pointer;
  text-align: center;
}

.quote2 > span:nth-child(1) {
  font-size: var(--fs-14-2);
  color: #000072;
  margin-top: 1%;
}

.quote2 > span:nth-child(2) {
  font-size: var(--fs-16-2);
  color: #158a5a;
  margin-top: 5%;
}

.page3Container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.page3Content {
  position: relative;
  width: calc(var(--ingame-width) * 0.8);
  height: calc(var(--ingame-width) * 0.8 * 423 / 328);
  background: linear-gradient(173.16deg, #ffffff 5.35%, #dbe1e8 101.95%, #e7f0fb 101.95%);
  border-radius: calc(var(--ingame-width) * 0.05);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.quote3 {
  text-align: center;
  font-size: var(--fs-14-2);
  color: #000072;
  margin-top: 7%;
}

.vplogo {
  height: 38%;
  margin-top: 4%;
}

.actionButton {
  width: calc(var(--ingame-width) * 0.5);
  height: calc(var(--ingame-width) * 0.5 * 57.45 / 206.31);
  background-size: 100% 100%;
}

.exitButton {
  width: calc(var(--ingame-width) * 0.1);
  height: calc(var(--ingame-width) * 0.1);
  background-size: 100% 100%;
}

.continueButton {
  width: calc(var(--ingame-width) * 0.5);
  height: calc(var(--ingame-width) * 0.5 * 57.45 / 206.31);
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: var(--fs-20);
  text-shadow: calc(var(--game-width) * -0.003) calc(var(--game-width) * 0.003 * 2) 0px
    rgba(155, 3, 10, 0.6);
}
