.container {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #158a5a;
  font-size: var(--fs-14-2);
  top: calc(var(--ingame-width) * 1.5);
  left: calc(var(--ingame-width) * 0.6);
}

.hide {
  visibility: hidden;
  background-size: 200% 200%;
  opacity: 0;
}

.show {
  visibility: visible;
  width: calc(var(--ingame-width) * 0.36);
  height: calc(var(--ingame-width) * 0.36 * 79 / 148);
  background-size: 100% 100%;
  opacity: 1;
}
