.container {
  display: flex;
  flex: 1;
  width: 100%;
  overflow: auto;

  font-family: SVNGilroyRegular;
  display: flex;
  flex-wrap: wrap;
  overflow: scroll;
  border: 1px solid #00000000;
  border-radius: 10px;

  height: calc(100% - var(--game-width) * 0.295 * 0.5);

  overflow-y: scroll;
}

.main_content {
  background: linear-gradient(180deg, #adce69 0%, #56b892 100%);
  width: 100%;
  height: fit-content;
  padding: calc(var(--padding-horizontal) * 2) calc(var(--padding-horizontal) * 1)
    calc(var(--padding-horizontal) * 1.5) calc(var(--padding-horizontal) * 1);
  border: 1px solid #024436;
  border-radius: 10px;
  position: relative;
  z-index: 2;
}

.gift_content {
  position: relative;
  width: 100%;
  padding: var(--padding-horizontal) var(--padding-horizontal) calc(var(--padding-horizontal) * 1.5)
    calc(var(--padding-horizontal) * 2);
  z-index: 3;
  min-height: calc(var(--padding-horizontal) * 2.5 + var(--game-width) * 0.1 * 1.46);
}

.second {
  margin-top: calc(var(--padding-horizontal) * 1);
  padding-top: calc(var(--padding-horizontal) * 1);
}

.bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.content {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: calc(var(--padding-horizontal) * 0.2);
  min-height: calc(var(--game-width) * 0.1 * 1.46);
}

.giftBtn {
  width: 100%;
  z-index: 4;
}

.text {
  width: calc(var(--game-width) * 0.48);
  z-index: 4;
  margin-right: 2%;
  padding-right: 6%;
  min-height: calc(var(--game-width) * 0.1 * 1.46);
  border-left: 0.5px solid #d0a26c;
  padding-left: calc(var(--padding-horizontal) * 0.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.btn {
  width: calc(var(--game-width) * 0.26);
  z-index: 4;
  margin-right: calc(var(--padding-horizontal) * 0.5);
}

.btn_title {
  z-index: 4;
  margin: auto;
  text-shadow: -1px 2px 0px #000072;
  line-height: calc(var(--game-width) * 0.26 * 0.33);
  font-size: var(--fs-13);
}

.btn_bg {
  z-index: 4;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.title {
  color: #000072;
  font-size: var(--fs-14);
}

.description {
  color: #000000;
  font-size: var(--fs-12);
}

.detail {
  font-size: var(--fs-12);
  z-index: 3;
}

.bottom {
  z-index: 4;
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  color: #13734b;
  font-size: var(--fs-12);
  align-items: center;
}

.catCard {
  width: calc(var(--game-width) * 0.042);
}

.ticketGain {
  margin: 0 calc(var(--padding-horizontal) * 0.3);
}

.limit {
  margin: 0 calc(var(--padding-horizontal) * 0.3);
}
.realtime {
  margin-left: calc(var(--padding-horizontal) * 0.3);
}

.more {
  color: #ec141c;
  font-size: var(--fs-11);
  font-style: italic;
  z-index: 4;
}

.main_title {
  text-align: center;
  color: #000072;
  font-size: var(--fs-24);
}

.main_title_2 {
  text-align: center;
  color: #000072;
  font-size: var(--fs-24);
  margin-bottom: calc(var(--padding-horizontal) * 1);
}

.main_limit_container {
  margin-top: calc(var(--padding-horizontal) * 0.5);
  margin-bottom: calc(var(--padding-horizontal) * 0.5);
  font-size: var(--fs-14);
  color: #000072;
  justify-content: center;
}

.center {
  margin: 0 calc(var(--padding-horizontal) * 0.5);
}

.moc1 {
  position: absolute;
  width: calc(var(--game-width) * 0.038);
  top: calc(var(--game-width) * 0.038 * -1.5);
  left: calc(var(--game-width) * 0.03);
  z-index: 3;
}

.moc2 {
  position: absolute;
  width: calc(var(--game-width) * 0.038);
  top: calc(var(--game-width) * 0.038 * -1.5);
  right: calc(var(--game-width) * 0.03);
  z-index: 3;
}

.gift {
  width: calc(var(--game-width) * 0.1);
  z-index: 3;
  margin-right: calc(var(--padding-horizontal) * 2);
}

.giftCenter {
  border-right: 0.5px solid #d0a26c;
}

.giftContainer {
  z-index: 3;
  position: relative;
}

.money {
  position: absolute;
  z-index: 4;
  top: calc(var(--padding-horizontal) * 1.5);
  left: 0;
  text-align: center;
  width: calc(var(--game-width) * 0.1);
}

.value {
  font-size: calc(var(--fs-12) / 2.4);
  color: #df1818;
}

.vnd {
  font-size: calc(var(--fs-12) / 2.4);
  color: #df1818;
}
