.container {
  position: absolute;
  z-index: 2;
  top: 0;
  background: radial-gradient(circle at center, transparent 25%, rgba(1, 1, 1, 0.8) 27%);
  background-position: 50% 50%;
  transition: background-size 0.5s ease-out, opacity 0.5s ease-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

.hide {
  visibility: hidden;
  background-size: 200% 200%;
  opacity: 0;
}

.show {
  visibility: visible;
  width: var(--game-width);
  height: var(--app-height);
  background-size: 100% 100%;
  opacity: 1;
}

.quote {
  position: absolute;
  width: calc(var(--ingame-width) * 0.8);
  height: calc(var(--ingame-width) * 0.8 * 133 / 328);
  background: linear-gradient(173.16deg, #ffffff 5.35%, #dbe1e8 101.95%, #e7f0fb 101.95%);
  border-radius: calc(var(--ingame-width) * 0.05);
  display: flex;
  justify-content: center;
  align-items: center;
  color: #158a5a;
  font-size: var(--fs-20-2);
  top: 68%;
  text-align: center;
}
