.container {
  position: absolute;
  z-index: 4;
  top: 0;
  left: 0;
  background-color: rgba(1, 1, 1, 0.8);
  width: var(--game-width);
  height: var(--app-height);
  opacity: 1;
  overflow: hidden;
}

.hide {
  visibility: hidden;
  opacity: 0;
}

.content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.text1 {
  font-size: var(--fs-24-2);
  color: white;
  margin-top: 25%;
  text-align: center;
}

.text2 {
  font-size: var(--fs-48-2);
  color: white;
  text-align: center;
}

.text3 {
  font-size: var(--fs-24-2);
  color: #f8c283;
  margin-top: 2%;
  text-align: center;
}

.chestWrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 17%;
}

.chestGlow {
  position: absolute;
  z-index: 1;
  height: calc(var(--ingame-width) * 0.55);
  top: calc(var(--ingame-width) * -0.15);
}

.chest {
  position: relative;
  z-index: 2;
  height: calc(var(--ingame-width) * 0.46);
}

.text4 {
  font-size: var(--fs-20-2);
  margin-top: 9%;
  text-align: center;
  /* animation: textBlink;
  animation-duration: 1.5s;
  animation-iteration-count: infinite; */
}

@keyframes textBlink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
