.container {
  position: relative;
  width: calc(var(--game-width) * 0.94);
  height: calc(var(--game-width) * 0.94 * 502 / 397);
  background-size: 100% 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.headerImage {
  position: absolute;
  height: 43%;
  top: -23.5%;
}

.info {
  width: 83.5%;
  height: 72%;
  background: linear-gradient(173.16deg, #ffffff 5.35%, #dbe1e8 101.95%, #e7f0fb 101.95%);
  border-radius: calc(var(--game-width) * 0.05);
  margin-top: 16%;

  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 2%;

  overflow-x: hidden;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.info::-webkit-scrollbar {
  display: none;
}

.text1 {
  text-align: center;
  color: #000072;
  font-size: var(--fs-17);
  margin-top: 25%;
  white-space: pre-line;
}

.text2 {
  text-align: center;
  color: #158a5a;
  font-size: var(--fs-17);
  margin-top: 10%;
  white-space: pre-line;
}

.actionRow {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2%;
  padding: 0 8%;
}

.actionButton {
  width: calc(var(--game-width) * 0.38);
  height: calc(var(--game-width) * 0.38 * 65 / 161);
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: var(--fs-20);
  text-shadow: calc(var(--game-width) * -0.003) calc(var(--game-width) * 0.003 * 2) 0px
    rgba(155, 3, 10, 0.6);
}

.exitButton {
  width: calc(var(--game-width) * 0.08);
  height: calc(var(--game-width) * 0.08);
  background-size: 100% 100%;
}
