.container {
  position: relative;
  width: var(--game-width);
  height: var(--app-height);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: -13%;
}

.headerImage {
  position: relative;
  z-index: 2;
  width: calc(var(--game-width) * 0.9);
  margin-left: -3%;
  margin-bottom: -6%;
}

.content {
  position: relative;
  z-index: 1;
  width: calc(var(--game-width) * 0.85);
  height: calc(var(--game-width) * 0.85 * 482 / 381);
  border-radius: calc(var(--game-width) * 0.025);
  background: linear-gradient(173.16deg, #ffffff 5.35%, #dbe1e8 101.95%, #e7f0fb 101.95%);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 7%;
}

.info {
  width: 100%;
  height: 78%;

  overflow-x: hidden;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.info::-webkit-scrollbar {
  display: none;
}

.actionRow {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 4%;
  padding: 0 5%;
}

.actionButton {
  width: calc(var(--game-width) * 0.36);
  height: calc(var(--game-width) * 0.36 * 65 / 161);
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: var(--fs-20);
  text-shadow: calc(var(--game-width) * -0.003) calc(var(--game-width) * 0.003 * 2) 0px
    rgba(155, 3, 10, 0.6);
}

.exitButton {
  width: calc(var(--game-width) * 0.08);
  height: calc(var(--game-width) * 0.08);
  background-size: 100% 100%;
}

.contentWrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logoWrapper {
  position: relative;
  height: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5%;
}

.logoWrapper img {
  height: 90%;
}

.logoWrapper span {
  position: absolute;
  font-size: var(--fs-10);
  color: #df1818;
  margin-left: 4%;
  top: 33%;
}

.rewardText {
  font-size: var(--fs-24);
  color: #d81c23;
  text-align: center;
  margin-top: 3%;
}

.subText {
  font-size: var(--fs-14);
  color: #000072;
  text-align: center;
  margin-top: 4%;
}

.contentWrapper2 {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logoWrapper2 {
  position: relative;
  height: 50%;
  width: 100%;
  margin-top: 5%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.vpPieces {
  position: relative;
  height: 100%;
  z-index: 1;
}

.vpPieces2 {
  position: relative;
  height: 100%;
  z-index: 1;
  opacity: 0.6;
}

.piece {
  position: absolute;
  height: 100%;
  z-index: 2;
}

.rewardText2 {
  font-size: var(--fs-20);
  color: #d81c23;
  text-align: center;
  margin-top: 3%;
}
