.container {
  display: flex;
  flex: 1;
  width: 100%;
  overflow: auto;

  font-family: SVNGilroyRegular;
  display: flex;
  flex-wrap: wrap;
  overflow: scroll;
  border: 1px solid #00000000;
  border-radius: 10px;

  height: calc(100% - var(--game-width) * 0.295 * 0.5);
  z-index: 2;

  overflow-x: hidden;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.container::-webkit-scrollbar {
  display: none;
}

.main_content {
  background: linear-gradient(180deg, #adce69 0%, #56b892 100%);
  width: 100%;
  height: fit-content;
  padding: calc(var(--padding-horizontal) * 2) calc(var(--padding-horizontal) * 1)
    calc(var(--padding-horizontal) * 1.5) calc(var(--padding-horizontal) * 1);
  border: 1px solid #024436;
  border-radius: 10px;
}

.mission {
  position: relative;
  width: 100%;
  margin-bottom: calc(var(--padding-horizontal) * 0.8);
  padding: var(--padding-horizontal) var(--padding-horizontal) calc(var(--padding-horizontal) * 1)
    calc(var(--padding-horizontal) * 2);
}

.second {
  margin-top: calc(var(--padding-horizontal) * 1.2);
  padding-top: calc(var(--padding-horizontal) * 1);
}

.bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.content {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: calc(var(--padding-horizontal) * 0.2);
}

.text {
  width: calc(var(--game-width) * 0.48);
  z-index: 2;
}

.longText {
  width: calc(var(--game-width) * 0.74);
  z-index: 2;
}

.btn {
  width: calc(var(--game-width) * 0.26);
  z-index: 2;
  margin-right: calc(var(--padding-horizontal) * 0.5);
}

.btn_title {
  z-index: 2;
  margin: auto;
  text-shadow: -1px 2px 0px #000072;
  line-height: calc(var(--game-width) * 0.26 * 0.33);
  font-size: var(--fs-13);
}

.done_btn_title {
  z-index: 2;
  margin: auto;
  color: #000072;
  line-height: calc(var(--game-width) * 0.26 * 0.26);
  font-size: var(--fs-13);
}

.btn_bg {
  z-index: 2;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.title {
  color: #158a5a;
  font-size: var(--fs-14);
}

.description {
  color: #2d2d2d;
  font-size: var(--fs-11);
}

.bottom {
  z-index: 3;
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  color: #13734b;
  font-size: var(--fs-12);
  align-items: flex-end;
  justify-content: space-around;
}

.bottom2 {
  z-index: 3;
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  color: #13734b;
  font-size: var(--fs-12);
  align-items: flex-end;
}

.catCard {
  width: calc(var(--game-width) * 0.042);
}

.ticketGain {
  margin: 0 calc(var(--padding-horizontal) * 0.1);
}

.limit {
  margin: 0 calc(var(--padding-horizontal) * 0.2);
}
.realtime {
  margin-left: calc(var(--padding-horizontal) * 0.1);
}

.more {
  color: #ec141c;
  font-size: var(--fs-11);
  font-style: italic;
  z-index: 3;
}

.main_title {
  text-align: center;
  color: #000072;
  font-size: var(--fs-24);
}

.main_title_2 {
  text-align: center;
  color: #000072;
  font-size: var(--fs-24);
  margin-bottom: calc(var(--padding-horizontal) * 1);
}

.main_limit_container {
  margin-top: calc(var(--padding-horizontal) * 0.5);
  margin-bottom: calc(var(--padding-horizontal) * 0.5);
  font-size: var(--fs-14);
  color: #000072;
  justify-content: center;
}

.center {
  margin: 0 calc(var(--padding-horizontal) * 0.5);
}

.specialBottom {
  margin-top: calc(var(--padding-horizontal) * 0.7);
}

.specialCenter {
  width: 60%;
  border-top: 0.5px solid #a7b4d5;
  position: relative;
  z-index: 4;
  margin-left: 20%;
  margin-top: calc(var(--padding-horizontal) * 0.7);
}
