.container {
  position: relative;
  width: calc(var(--game-width) * 0.9);
  height: calc(var(--game-width) * 0.9 * 290 / 332);
  background-size: 100% 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px calc(var(--game-width) * 0.07);
  padding-top: calc(var(--game-width) * 0.05);
}

.closeButton {
  width: calc(var(--game-width) * 0.085);
  height: calc(var(--game-width) * 0.085);
  background-size: 100% 100%;
}

.title {
  color: #000072;
  font-size: var(--fs-20);
  text-align: center;
  flex: 1;
  justify-content: center;
  align-items: center;
  display: flex;
  padding: 2%;
}
