* {
  box-sizing: border-box;
}

:root {
  /* Theme */
  --main-color: white;

  --primary-color: #22c582;

  --fs-10: calc(var(--game-width) * 0.025);
  --fs-11: calc(var(--game-width) * 0.028);
  --fs-12: calc(var(--game-width) * 0.03);
  --fs-13: calc(var(--game-width) * 0.0315);
  --fs-14: calc(var(--game-width) * 0.033);
  --fs-15: calc(var(--game-width) * 0.036);
  --fs-16: calc(var(--game-width) * 0.038);
  --fs-17: calc(var(--game-width) * 0.043);
  --fs-18: calc(var(--game-width) * 0.046);
  --fs-20: calc(var(--game-width) * 0.05);
  --fs-24: calc(var(--game-width) * 0.054);
  --fs-32: calc(var(--game-width) * 0.078);

  --fs-10-2: calc(var(--ingame-width) * 0.026);
  --fs-11-2: calc(var(--ingame-width) * 0.028);
  --fs-12-2: calc(var(--ingame-width) * 0.03);
  --fs-13-2: calc(var(--ingame-width) * 0.0315);
  --fs-14-2: calc(var(--ingame-width) * 0.033);
  --fs-15-2: calc(var(--ingame-width) * 0.036);
  --fs-16-2: calc(var(--ingame-width) * 0.038);
  --fs-18-2: calc(var(--ingame-width) * 0.046);
  --fs-20-2: calc(var(--ingame-width) * 0.05);
  --fs-24-2: calc(var(--ingame-width) * 0.054);
  --fs-32-2: calc(var(--ingame-width) * 0.078);
  --fs-48-2: calc(var(--ingame-width) * 0.108);

  --inset-top: env(safe-area-inset-top, 0);
  --inset-bottom: env(safe-area-inset-bottom, 0);
  --inset-left: env(safe-area-inset-left, 0);
  --inset-right: env(safe-area-inset-right, 0);
}

html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  overflow: -moz-hidden-unscrollable;
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  user-select: none;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell,
    Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  width: 100%;
  height: 100%;
  justify-content: center;
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
  color: var(--main-color);

  /* position: fixed; */
}

#root {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
}

#fps {
  position: absolute;
  z-index: 2;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@font-face {
  font-family: SVNGilroyBold;
  src: url(../public/assets/fonts/SVN-Gilroy-Bold.otf);
}

@font-face {
  font-family: SVNGilroyMedium;
  src: url(../public/assets/fonts/SVN-Gilroy-Medium.otf);
}

@font-face {
  font-family: SVNGilroyRegular;
  src: url(../public/assets/fonts/SVN-Gilroy-Regular.otf);
}

@font-face {
  font-family: SVNGilroySemiBold;
  src: url(../public/assets/fonts/SVN-Gilroy-SemiBold.otf);
}

/* Utilities */
.noselect,
.noselect * {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
  -webkit-user-drag: none;
  outline: none;
}

input {
  user-select: auto !important;
}

input::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #b7b7b7;
  opacity: 1; /* Firefox */
}

input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #b7b7b7;
}

input::-ms-input-placeholder {
  /* Microsoft Edge */
  color: #b7b7b7;
}

.sg-regular {
  font-family: SVNGilroyRegular;
}

.sg-bold {
  font-family: SVNGilroyBold;
}

.sg-semibold {
  font-family: SVNGilroySemiBold;
}

.sg-medium {
  font-family: SVNGilroyMedium;
}

.row {
  display: flex;
  flex-direction: row;
}

.img {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -drag: none;
  user-drag: none;
}

.hidden {
  visibility: hidden;
}
