.container {
  position: relative;
  width: calc(var(--game-width) * 0.8);
  height: calc(var(--game-width) * 0.8 * 473 / 394);
  background-size: 100% 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* padding: 0px calc(var(--game-width) * 0.07); */
  padding-top: calc(var(--game-width) * 0.18);
  margin-top: -15%;
}

.closeButton {
  width: calc(var(--game-width) * 0.085);
  height: calc(var(--game-width) * 0.085);
  background-size: 100% 100%;
}

.header {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 5%;
  justify-content: center;
}

.toggleOn img {
  filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(7488%) hue-rotate(130deg)
    brightness(102%) contrast(104%);
}

.ruleLink {
  font-size: var(--fs-18);
  color: #000072;
  text-decoration: underline;
  text-underline-offset: calc(var(--game-width) * 0.015);
  margin-top: 17%;
  cursor: pointer;
}

.custom {
  font-size: var(--fs-24);
  white-space: pre-line;
}
.taiLocTitle {
  font-size: var(--fs-24);

  white-space: pre-line;
  text-align: center;
  margin-top: calc(var(--padding-horizontal) * -2);
}

.gift {
  margin-top: calc(var(--padding-horizontal) * 2);
  height: calc(var(--game-width) * 0.5);
}

.moc1 {
  position: absolute;
  width: calc(var(--game-width) * 0.042);
  bottom: calc(var(--game-width) * 0.295 * -0.23);
  left: calc(var(--game-width) * 0.15);
  z-index: 2;
}

.moc2 {
  position: absolute;
  width: calc(var(--game-width) * 0.042);
  bottom: calc(var(--game-width) * 0.295 * -0.23);
  right: calc(var(--game-width) * 0.15);
  z-index: 2;
}

.bottom {
  background: linear-gradient(180deg, #adce6a 0%, #58b992 100%);
  padding: calc(var(--padding-horizontal) * 3) calc(var(--padding-horizontal) * 4);
  z-index: 2;
  border: 1px solid #024436;
  border-radius: 10px;
  width: 99%;
  margin-left: 1%;
  margin-top: calc(var(--padding-horizontal) * 1);
  text-align: center;
  width: calc(var(--game-width) * 0.8);
  display: flex;
  align-items: center;
  flex-direction: column;
}

.date {
  margin-bottom: calc(var(--padding-horizontal) * 1);
  color: #000072;
  font-size: var(--fs-16);
}

.congrat {
  color: #000;
  font-size: var(--fs-14);
}

.money {
  position: absolute;
  top: calc(var(--padding-horizontal) * 10);
  width: calc(var(--game-width) * 0.75 * 0.5);
  left: 0;
  text-align: center;
}

.giftContainer {
  position: relative;
}

.value {
  font-size: var(--fs-14);
  color: #df1818;
  margin-left: calc(var(--fs-14) / 2);
}
