.container {
  width: var(--app-width);
  height: var(--app-height);

  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #1d4289;
}

.main {
  display: flex;
  width: var(--game-width);
  height: var(--app-height);
}

.wideMain {
  display: flex;
  width: var(--app-width);
  height: var(--app-height);
  background-color: white;
  flex-direction: column;
}

.content {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  padding-top: var(--inset-top);
}
