.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: var(--padding-horizontal);

  overflow-x: hidden;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  overflow-y: hidden;
}

.background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.backgroundText {
  position: absolute;
  top: calc(var(--game-width) * 0.1);
  left: calc(var(--game-width) * 0.18);
  width: calc(var(--game-width) * 0.65);
}

.content {
  position: absolute;
  top: calc(39vh - var(--game-width) * 0.08);
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 2;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  z-index: 2;
}

.back_btn {
  width: calc(var(--game-width) * 0.08);
}

.hello_title {
  font-size: var(--fs-16);
  color: var(--main-color);
  align-self: center;
}

.header_btn_2 {
  width: calc(var(--game-width) * 0.08);
  visibility: hidden;
}

.btn {
  margin-bottom: calc(var(--game-width) * 0.02);
  width: calc(var(--game-width) * 0.65);
}

.btn_title {
  z-index: 1;
  margin: auto;
  text-shadow: -1px 2px 0px #000072;
  line-height: calc(var(--game-width) * 0.65 * 0.206);
  font-size: var(--fs-24);
}

.btn_bg {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.turns {
  font-size: var(--fs-16);
  z-index: 2;
  margin-bottom: calc(var(--game-width) * 0.02);
}

.bottom {
  margin-top: auto;
  z-index: 2;
  width: 100%;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.catStory {
  white-space: wrap;
  font-size: var(--fs-18);
  text-align: right;
  white-space: pre-line;
}

.mrTop {
  margin-top: calc(var(--game-width) * 0.03);
}

.catStoryImg {
  width: calc(var(--game-width) * 0.24);
  margin-left: calc(var(--game-width) * 0.02);
}

.catStoryBox {
  margin-left: auto;
}

.rule {
  text-decoration: underline;
  font-size: var(--fs-18);
  color: #fceda3;
  margin: calc(var(--game-width) * 0.02) 0;
}
