.container {
  position: absolute;
  width: calc(var(--ingame-width));
  display: flex;
  justify-content: center;
  margin-top: calc(var(--ingame-width) * 0.06);
}

.contentWrapper {
  width: var(--ingame-width);
}

.content {
  width: var(--ingame-width);
  height: calc(var(--ingame-width) * 66 / 412);
  background-size: 100% 100%;
  display: flex;
  padding-top: calc(var(--ingame-width) * 0.02);
  padding-left: calc(var(--ingame-width) * 0.035);
}

.timer {
  width: calc(var(--ingame-width) * 0.1);
  height: calc(var(--ingame-width) * 0.1);
  background-size: 100% 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: calc(var(--ingame-width) * 0.062);
}

.timer span {
  font-family: SVNGilroyMedium;
  color: #13734b;
  font-size: var(--fs-11-2);
}

.scoreWrapper {
  margin-left: 2%;
  padding-top: 0.8%;
}

.score {
  color: #000072;
  font-size: var(--fs-14-2);
}

.scoreProgress {
  --progress-bar-width: calc(var(--ingame-width) * 0.6);
  --progress-bar-height: calc(var(--ingame-width) * 0.018);

  display: flex;
  flex-direction: column;
  width: var(--progress-bar-width);
  margin-top: calc(var(--ingame-width) * 0.035);
}

.progressMilestones {
  position: relative;
  width: var(--progress-bar-width);
  display: flex;
  align-items: center;
}

.milestone {
  position: absolute;
  display: flex;
  justify-content: center;
  color: #000072;
  top: calc(var(--ingame-width) * -0.034);
  font-size: var(--fs-14-2);
  transform: translateX(-50%);
}

.progressBarWrapper {
  width: var(--progress-bar-width);
  height: var(--progress-bar-height);
  border-radius: calc(var(--ingame-width) * 0.2);
}

.progressBar {
  position: relative;
  z-index: 1;
  width: var(--progress-bar-width);
  height: var(--progress-bar-height);
  border-radius: calc(var(--ingame-width) * 0.2);
  transition: width 0.5s ease-out;
}

.chestWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-left: calc(var(--ingame-width) * 0.07);
  margin-top: calc(var(--ingame-width) * -0.05);
}

.chestWrapper img {
  height: calc(var(--ingame-width) * 0.07);
}

.chestWrapper span {
  font-size: var(--fs-10-2);
  text-align: center;
}
