.header {
  text-align: center;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  padding: var(--padding-horizontal);
  z-index: 2;
}

.settings_btn {
  width: calc(var(--game-width) * 0.08);
}

.btn {
  width: calc(var(--game-width) * 0.3);
}

.username {
  font-size: var(--fs-16);
}

.back_btn {
  width: calc(var(--game-width) * 0.08);
}
