.container {
  position: relative;
  width: calc(var(--game-width) * 0.9);
  height: calc(var(--game-width) * 0.9 * 290 / 332);
  background-size: 100% 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px calc(var(--game-width) * 0.07);
  padding-top: calc(var(--game-width) * 0.05);
}

.closeButton {
  width: calc(var(--game-width) * 0.085);
  height: calc(var(--game-width) * 0.085);
  background-size: 100% 100%;
}

.header {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 5%;
  justify-content: center;
}

.header img {
  height: calc(var(--game-width) * 0.08);
}

.header span {
  color: #db6c13;
  font-size: var(--fs-24);
  margin-left: 4%;
}

.settingRow {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-top: 9%;
  color: #000072;
}

.settingRow span:first-child {
  font-size: var(--fs-18);
  color: var(--main-color);
}

.settingWrapper {
  width: 60%;
  height: calc(var(--game-width) * 0.1);
  display: flex;
  align-items: center;
}

.settingLeft {
  width: 49%;
  height: 100%;
  background-color: #f5e7d4;
  margin-right: 2%;
  border-radius: calc(var(--game-width) * 0.06) 0 0 calc(var(--game-width) * 0.06);
  display: flex;
  align-items: center;
  justify-content: center;
  /* color: #929292; */
}

.settingRight {
  width: 49%;
  height: 100%;
  background-color: #f5e7d4;
  border-radius: 0 calc(var(--game-width) * 0.06) calc(var(--game-width) * 0.06) 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.settingLeft img,
.settingRight img {
  height: 50%;
  margin-right: 2%;
  /* filter: brightness(0) saturate(100%) invert(74%) sepia(45%) saturate(782%) hue-rotate(331deg)
    brightness(100%) contrast(91%); */
}

.toggleOn {
  background: linear-gradient(180deg, #22c582 0%, #006952 100%);
  color: white;
}

.toggleOn img {
  filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(7488%) hue-rotate(130deg)
    brightness(102%) contrast(104%);
}

.ruleLink {
  font-size: var(--fs-18);
  color: #000072;
  text-decoration: underline;
  text-underline-offset: calc(var(--game-width) * 0.015);
  margin-top: 32%;
  cursor: pointer;
}

.soundFX {
  color: #000072;
  font-size: var(--fs-20);
}

.custom {
  font-size: var(--fs-32);
  margin-top: calc(var(--padding-horizontal) * 0.3);
}

.enableTitle {
  font-size: var(--fs-16);
  color: #fff;
}

.disableTitle {
  font-size: var(--fs-16);
  color: #929292;
}
