.loader-modal-container {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: var(--app-width);
  height: var(--app-height);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.full-screen-loader {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 9999;
}
