.container {
  position: absolute;
  width: calc(var(--ingame-width));
  display: flex;
  justify-content: center;
  margin-top: calc(var(--ingame-width) * 1.87);
}

.contentWrapper {
  width: var(--ingame-width);
}

.content {
  width: var(--ingame-width);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 4%;
}

.homeButton {
  width: calc(var(--ingame-width) * 0.19);
  height: calc(var(--ingame-width) * 0.19 * 36 / 89.22);
  background-size: 100% 100%;
}

.homeButtonPlaceholder {
  width: calc(var(--ingame-width) * 0.19);
  height: calc(var(--ingame-width) * 0.19 * 36 / 89.22);
}

.turnWrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-right: 2%;
  padding-left: 6%;
  background-color: #000072;
  border-radius: calc(var(--ingame-width) * 0.1);
  height: calc(var(--ingame-width) * 0.057);
}

.turnWrapper img {
  position: absolute;
  left: 0;
  height: calc(var(--ingame-width) * 0.08);
  margin-top: calc(var(--ingame-width) * -0.005);
  margin-right: calc(var(--ingame-width) * 0.012);
}

.turnWrapper span {
  color: white;
  font-size: var(--fs-16-2);
  margin-top: calc(var(--ingame-width) * 0.01);
}
