.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: var(--padding-horizontal);

  overflow-x: hidden;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  overflow-y: hidden;
  position: relative;
}

.header {
  text-align: center;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.settings_btn {
  width: calc(var(--game-width) * 0.08);
}

.btn {
  width: calc(var(--game-width) * 0.35);
}

.btn_title {
  z-index: 1;
  margin: auto;
  text-shadow: -1px 2px 0px #000072;
  line-height: calc(var(--game-width) * 0.35 * 0.4);
  font-size: var(--fs-20);
}

.btn_bg {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.bottom {
  margin-top: auto;
  z-index: 2;
}

.username {
  font-size: var(--fs-16);
}

.title {
  font-size: var(--fs-24);
  text-transform: uppercase;
  margin-top: calc(var(--game-width) * 0.1);
  line-height: calc(var(--game-width) * 0.02);
  z-index: 2;
}

.username {
  font-size: var(--fs-16);
}

.content {
  /* position: absolute; */
  margin: calc(20vh - var(--game-width) * 0.12) var(--padding-horizontal)
    calc(var(--padding-horizontal) * 2) var(--padding-horizontal);
  display: flex;
  flex: 1;
  width: 100%;
  overflow: auto;
  background: linear-gradient(180deg, #adce69 0%, #56b892 100%);
  border: 1px solid #024436;
  border-radius: 10px;
  padding: calc(var(--padding-horizontal) * 1.5) calc(var(--padding-horizontal) * 1);
  font-family: SVNGilroyRegular;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  z-index: 2;
}

.gifts {
  width: 100%;
  display: flex;
  overflow: auto;
  flex-wrap: wrap;
  justify-content: space-between;
  height: fit-content;
}

.otherGift {
  background: linear-gradient(173.16deg, #ffffff 5.35%, #dbe1e8 101.95%, #e7f0fb 101.95%);
  border: 1px solid #024436;
  border-radius: 10px;
  flex-direction: row;
  display: flex;
  flex: 1;
  padding: calc(var(--padding-horizontal));
  align-items: center;
}

.lixi {
  width: calc(var(--game-width) * 0.26);
}

.otherRewards {
  color: #158a5a;
  font-size: var(--fs-14);
  margin-left: var(--padding-horizontal);
}
