.container {
  display: flex;
  flex: 1;
  width: 100%;
  overflow: auto;

  font-family: SVNGilroyRegular;
  display: flex;
  flex-wrap: wrap;
  overflow: scroll;
  border: 1px solid #00000000;
  border-radius: 10px;

  height: calc(100% - var(--game-width) * 0.295 * 0.5);

  overflow-y: scroll;
}

.main_content {
  background: linear-gradient(180deg, #adce69 0%, #56b892 100%);
  width: 100%;
  height: fit-content;
  padding: calc(var(--padding-horizontal) * 2) calc(var(--padding-horizontal) * 1)
    calc(var(--padding-horizontal) * 1.5) calc(var(--padding-horizontal) * 1);
  border: 1px solid #024436;
  border-radius: 10px;
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main_title {
  text-align: center;
  color: #000072;
  font-size: var(--fs-14);
}

.pieces {
  margin-top: calc(var(--padding-horizontal) * 2);
  height: calc(var(--game-width) * 0.82);
  width: calc(var(--game-width) * 0.82);
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: calc(var(--padding-horizontal) * 1);
}

.piecesBg {
  width: calc(var(--game-width) * 0.82);
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.content {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: calc(var(--padding-horizontal) * 2);
  position: relative;
  flex-direction: column;
}

.top_left_piece {
  position: absolute;
  z-index: 3;
  width: calc(var(--game-width) * 0.135);
  top: 0;
  left: calc(50% - var(--game-width) * 0.58 * 0.24);
}

.phat1 {
  top: calc(var(--game-width) * 0.05);
  left: calc(50% - var(--game-width) * 0.58 * 0.45);
}

.top_right_piece {
  position: absolute;
  z-index: 3;
  width: calc(var(--game-width) * 0.135);
  top: 0;
  right: calc(50% - var(--game-width) * 0.58 * 0.24);
}

.phat2 {
  top: calc(var(--game-width) * 0.05);
  right: calc(50% - var(--game-width) * 0.58 * 0.45);
}

.bottom_left_peace {
  position: absolute;
  z-index: 3;
  width: calc(var(--game-width) * 0.29);
  top: calc(var(--game-width) * 0.175);
  left: calc(50% - var(--game-width) * 0.58 * 0.51);
}

.phat3 {
  top: calc(var(--game-width) * 0.35);
  left: calc(50% - var(--game-width) * 0.58 * 0.51);
}

.bottom_right_piece {
  position: absolute;
  z-index: 3;
  width: calc(var(--game-width) * 0.29);
  top: calc(var(--game-width) * 0.175);
  right: calc(50% - var(--game-width) * 0.58 * 0.51);
}

.phat4 {
  top: calc(var(--game-width) * 0.35);
  right: calc(50% - var(--game-width) * 0.58 * 0.51);
}

.vp_pieces {
  width: calc(var(--game-width) * 0.58);
  z-index: 2;
}

.tvp {
  color: #000072;
  font-size: var(--fs-18);
  margin-top: calc(var(--padding-horizontal) * 1);
}

.tvpValue {
  color: #d81c23;
  font-size: var(--fs-16);
  margin-top: calc(var(--padding-horizontal) * 0.5);
}

.btn {
  width: calc(var(--game-width) * 0.3);
  margin-top: var(--padding-horizontal);
}

.btn_title {
  z-index: 1;
  margin: auto;
  text-shadow: -1px 2px 0px #000072;
  line-height: calc(var(--game-width) * 0.3 * 0.4);
  font-size: var(--fs-20);
}

.btn_bg {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.piecesCount {
  position: absolute;
  color: #ec141c;
  font-size: var(--fs-20);
  z-index: 3;
}
